import { HermesObservable, HermesOperatorFunction } from '../observable/hermes.observable';
import { subscriberForOperator } from './operator-utils';

export function hermesTap<T>(operation: (value: T) => void): HermesOperatorFunction<T, T> {
	return (source) => {
		return new HermesObservable(observer => {

			const subscriber = subscriberForOperator(observer, (value) => {
				operation(value);
				observer.next(value);
			});

			return source.subscribe(subscriber).getFinalize();
		});
	};
}
