import { Injectable } from '@angular/core';

import { DomainEvent } from './domain-event';
import { DomainEventStore } from './domain-event.store';
import { AggregateId } from '../tactical/aggregate/aggregate-id';
import { HermesSubject } from '../../../common/stream/observable/hermes.subject';


@Injectable()
export class DomainEventStream extends HermesSubject<DomainEvent<AggregateId>> {

	constructor(private readonly eventStore: DomainEventStore) {
		super();
	}

	next(event: DomainEvent<AggregateId>): void {
		super.next(event);
		this.eventStore.next(event);
	}
}
