import { MonoTypeOperatorFunction } from 'rxjs';
import { HermesSubject } from './stream/observable/hermes.subject';
import { hermesTakeUntil } from './stream/operator/hermes.take-until';
import { toRxJsObservable } from '../infrastructure/rxjs/to-rxjs-observable';
import { takeUntil } from 'rxjs/operators';

export abstract class Reactive {

	private readonly hermesUnsubscribe$ = new HermesSubject<void>();

	protected constructor() {
	}

	ngOnDestroy() {
		this.hermesUnsubscribe();
	}

	protected takeUntil<T>(): MonoTypeOperatorFunction<T> {
		return takeUntil(
			toRxJsObservable(this.hermesUnsubscribe$)
		);
	}

	protected hermesUnsubscribe(): void {
		this.hermesUnsubscribe$.next();
		this.hermesUnsubscribe$.complete();
	}

	protected hermesTakeUntil(): any {
		return hermesTakeUntil(this.hermesUnsubscribe$);
	}

	protected isNotStopped(): boolean {
		return !(this.hermesUnsubscribe$ as any).isCompleted;
	}

}
