import { HermesSubject } from './hermes.subject';
import { HermesBehaviorSubject } from './hermes.behavior-subject';
import { HermesReplaySubject } from './hermes.replay-subject';

export class HermesArchiveSubject<T> extends HermesSubject<T> {

	static of<T>(value?: T) {
		if (value !== undefined) {
			return new HermesBehaviorSubject<T>(value);
		} else {
			return new HermesReplaySubject<T>(1);
		}
	}

	private constructor() {
		super();
	}
}
