import { Injectable, OnDestroy } from '@angular/core';

import { CommandBus } from '../../../domain/command/command.bus';
import { Command } from '../../../domain/command/command';
import { CommandLogger } from '../../../domain/command/command.logger';
import { AggregateId } from '../../../domain/tactical/aggregate/aggregate-id';
import { hermesFilter } from '../../../../common/stream/operator/hermes.filter';
import { hermesTakeUntil } from '../../../../common/stream/operator/hermes.take-until';
import { HermesSubject } from '../../../../common/stream/observable/hermes.subject';


@Injectable()
export class ConsoleCommandLogger extends CommandLogger implements OnDestroy {

	private enabled = false;

	private unsubscribe$ = new HermesSubject<void>();

	constructor(commandBus: CommandBus) {
		super();

		commandBus
			.pipe(
				hermesFilter(() => this.enabled),
				hermesTakeUntil(this.unsubscribe$)
			)
			.subscribe((command: Command<AggregateId>) => {
				this.log(command);
			});
	}

	ngOnDestroy() {
		this.unsubscribe$.next();
		this.unsubscribe$.complete();
	}

	start(): void {
		this.enabled = true;
	}

	stop(): void {
		this.enabled = false;
	}

	protected print(command: Command<AggregateId>): void {
		// eslint-disable-next-line no-console
		console.log(command.toString(), command);
	}

}
