import { Inject, Injectable } from '@angular/core';

import { Command } from './command';
import { FILTERED_COMMAND_STREAM } from './filtered-command-stream';
import { AggregateRoot } from '../tactical/aggregate/aggregate-root';
import { CreateAggregateCommandHandlerImpl } from './create-aggregate/create-aggregate.command-handler-impl';
import { CommandHandlerImpl } from './handler/command-handler-impl';
import { AggregateId } from '../tactical/aggregate/aggregate-id';
import { CreateAggregateCommand } from './create-aggregate/create-aggregate.command';
import { CommandStream } from './command.stream';
import { HermesSubscription } from '../../../common/stream/subscription/hermes.subscription';
import { HermesSubscriber } from '../../../common/stream/observable/subscriber/hermes.subscriber';
import { HermesObservable } from '../../../common/stream/observable/hermes.observable';
import { hermesFilter } from '../../../common/stream/operator/hermes.filter';

@Injectable()
export class CommandBus<C = Command<AggregateId>> extends HermesObservable<C> {

	constructor(@Inject(FILTERED_COMMAND_STREAM) private readonly commandsStream: CommandStream) {
		super();
	}

	subscribe(
		next?: (value: C) => void,
		error?: (error: any) => void,
		complete?: () => void
	): HermesSubscription;
	subscribe(
		subscriber: HermesSubscriber<C>
	): HermesSubscription;
	subscribe(
		arg: any
	): HermesSubscription {

		return this.commandsStream.toObservable().subscribe(arguments[0], arguments[1], arguments[2]);
	}

	ofCommand<C2 extends C>(...commandTypes: Array<string>): any {
		return (this as CommandBus<any>)
			.commandsStream
			.toObservable()
			.pipe(
				hermesFilter((command: Command<AggregateId>) => {
					return commandTypes.some((commandType: string) => command.ofMessageType(commandType));
				})
			);
	}

	ofCommandHandler<C2 extends C>(...handlers: Array<CommandHandlerImpl<AggregateId, AggregateRoot<AggregateId>, Command<AggregateId>>>): any {
		return (this as CommandBus<any>)
			.commandsStream
			.toObservable()
			.pipe(
				hermesFilter((command: Command<AggregateId>) => {
					return handlers.some((handler: CommandHandlerImpl<AggregateId, AggregateRoot<AggregateId>, Command<AggregateId>>) => {
						return handler.forCommand(command);
					});
				})
			);
	}

	ofCreateAggregateHandler<C2 extends C>(
		...handlers: Array<CreateAggregateCommandHandlerImpl<any, AggregateRoot<AggregateId>, CreateAggregateCommand>>
	): any {

		return (this as CommandBus<any>)
			.commandsStream
			.toObservable()
			.pipe(
				hermesFilter((command: CreateAggregateCommand) => {
					return handlers.some((handler: CreateAggregateCommandHandlerImpl<any, AggregateRoot<AggregateId>, CreateAggregateCommand>) => {
						return handler.forCommand(command);
					});
				})
			);
	}

	ofNullHandler<C2 extends C>(
		handlers: Array<CommandHandlerImpl<AggregateId, AggregateRoot<AggregateId>, Command<AggregateId>>>,
		aggregateCommandHandlers: Array<CreateAggregateCommandHandlerImpl<AggregateId, AggregateRoot<AggregateId>, CreateAggregateCommand>>
	): any {

		return (this as CommandBus<any>)
			.commandsStream
			.toObservable()
			.pipe(
				hermesFilter((command: Command<AggregateId>) => {

					if (!handlers && !aggregateCommandHandlers) {
						return true;
					}

					let foundHandlerForCommand = true;

					if (handlers) {
						foundHandlerForCommand =
							!handlers.some((handler: CommandHandlerImpl<AggregateId, AggregateRoot<AggregateId>, Command<AggregateId>>) => {
								return handler.forCommand(command);
							});
					}

					if (aggregateCommandHandlers) {
						foundHandlerForCommand = foundHandlerForCommand &&
							!aggregateCommandHandlers.some((handler: CreateAggregateCommandHandlerImpl<AggregateId, AggregateRoot<AggregateId>, any>) => {
								return handler.forCommand(command);
							});
					}

					return foundHandlerForCommand;

				})
			);
	}

}
