import { HermesObservable } from './hermes.observable';
import { HermesSubscription } from '../subscription/hermes.subscription';
import { HermesSubscriber } from './subscriber/hermes.subscriber';
import { hermesEmptySubscription } from '../subscription/hermes-empty.subscription';

export class HermesSubject<T> extends HermesObservable<T> {

	/**
	 * After error
	 */
	private thrownError: Error | string = null;

	/**
	 * After complete
	 */
	private isCompleted: boolean = false;

	/**
	 * After unsubscribe
	 */
	private isClosed: boolean = false;

	private readonly subscribers: Array<HermesSubscriber<T>> = [];

	constructor() {
		super();
	}

	next(value: T): void {

		this.verifyNotClosed();

		if (this.isCompleted || this.thrownError !== null) {
			return;
		}

		for (const subs of this.subscribers) {
			subs.next(value);
		}
	}

	error(error: any): void {

		this.verifyNotClosed();

		if (this.isCompleted) {
			return;
		}

		this.thrownError = error;

		for (const subs of this.subscribers) {
			subs.error(error);
		}
		this.subscribers.length = 0;
	}

	complete(): void {

		this.verifyNotClosed();

		if (this.isCompleted) {
			return;
		}

		this.isCompleted = true;

		for (const subs of this.subscribers) {
			subs.complete();
		}
		this.subscribers.length = 0;
	}

	subscribe(
		next?: (value: T) => void,
		error?: (error: any) => void,
		complete?: () => void
	): HermesSubscription;
	subscribe(
		subscriber: HermesSubscriber<T>
	): HermesSubscription;
	subscribe(
		arg: any
	): HermesSubscription {

		this.verifyNotClosed();

		let subscriber: HermesSubscriber<T>;

		if (arg instanceof HermesSubscriber) {
			subscriber = arg;
		} else {
			subscriber = this.createSubscriber(arguments[0], arguments[1], arguments[2]);
		}

		if (this.thrownError !== null) {
			subscriber.error(this.thrownError);
			return hermesEmptySubscription();
		} else if (this.isCompleted) {
			subscriber.complete();
			return hermesEmptySubscription();
		} else {
			this.subscribers.push(subscriber);
			return this.getSubscription();
		}
	}

	unsubscribe(): void {
		this.isCompleted = true;
		this.isClosed = true;
		this.subscribers.length = 0;
	}

	toObservable(): HermesObservable<T> {
		return new HermesObservable((observer) => {
			const subscription = this.subscribe(
				(v) => observer.next(v),
				(error) => observer.error(error),
				() => observer.complete()
			);

			return () => subscription.unsubscribe();
		});
	}

	private verifyNotClosed(): void {
		if (this.isClosed) {
			throw new Error('Observable already closed');
		}
	}

}
