import { HermesArchiveSubject } from './stream/observable/hermes.archive-subject';
import { HermesObservable } from './stream/observable/hermes.observable';
import { hermesDistinctUntilChanged } from './stream/operator/hermes.distinct-until-changed';

export abstract class Archive<T> {

	private readonly archive$: HermesArchiveSubject<T>;

	protected constructor(value?: T) {
		this.archive$ = HermesArchiveSubject.of(value);
	}

	on(): HermesObservable<T> {
		return this.archive$
				   .toObservable()
				   .pipe(
					   hermesDistinctUntilChanged(this.compare)
				   );
	}

	next(value: T): void {
		this.archive$.next(value);
	}

	compare(one: T, two: T): boolean {
		return one === two;
	}
}
