import { HermesSubject } from './hermes.subject';
import { HermesSubscription } from '../subscription/hermes.subscription';
import { HermesSubscriber } from './subscriber/hermes.subscriber';

export class HermesBehaviorSubject<T> extends HermesSubject<T> {

	private lastValue: T;

	constructor(defaultValue: T) {
		super();
		this.lastValue = defaultValue;
	}

	next(value: T) {
		this.lastValue = value;
		super.next(value);
	}

	subscribe(
		next?: (value: T) => void,
		error?: (error: any) => void,
		complete?: () => void
	): HermesSubscription;
	subscribe(
		subscriber: HermesSubscriber<T>
	): HermesSubscription;
	subscribe(
		arg: any
	): HermesSubscription {

		const subscription = super.subscribe(arguments[0], arguments[1], arguments[2]);

		super.next(this.lastValue);

		return subscription;
	}
}
