import { HermesObserver } from '../hermes.observer';

export class HermesSubscriber<T> {

	private observer: Partial<HermesObserver<T>>;

	private finalize: () => void = () => {
	};

	private completed: boolean = false;

	private closed: boolean = false;

	constructor(config: Partial<HermesObserver<T>>) {
		this.observer = config;
	}

	next(value: T): void {

		if (this.isCompleted()) {
			return;
		}

		if (this.observer && this.observer.next) {
			this.observer.next(value);
		}
	}

	error(error: any): void {

		if (this.completed) {
			return;
		}

		if (this.observer && this.observer.error) {
			this.observer.error(error);
		}
		this.unsubscribe();
	}

	complete(): void {

		if (this.completed) {
			return;
		}

		this.completed = true;
		if (this.observer && this.observer.complete) {
			this.observer.complete();
		}

		this.unsubscribe();
	}

	unsubscribe(): void {
		if (this.closed) {
			return;
		}

		this.closed = true;
		this.completed = true;

		this.finalize();
	}

	setFinalize(finalize: () => void): void {
		if (finalize && typeof finalize === 'function') {
			this.finalize = finalize;
		} else {

		}
	}

	protected isCompleted(): boolean {
		return this.completed;
	}

	protected isClosed(): boolean {
		return this.closed;
	}

	protected getObserver(): Partial<HermesObserver<T>> {
		return this.observer;
	}

}
