import { ReactiveService } from './reactive.service';
import { KeyMap } from './collections/key-map';
import { Optional } from './optional';
import { hermesFilter } from './stream/operator/hermes.filter';
import { hermesMap } from './stream/operator/hermes.map';
import { hermesDistinctUntilChanged } from './stream/operator/hermes.distinct-until-changed';
import { HermesArchiveSubject } from './stream/observable/hermes.archive-subject';
import { HermesObservable } from './stream/observable/hermes.observable';
import { hermesTake } from './stream/operator/hermes.take';
import { Key } from './collections/key';


export abstract class KeyArchive<K extends Key, T> extends ReactiveService {

	private readonly archive = new KeyMap<K, T>();

	private readonly archive$: HermesArchiveSubject<KeyMap<K, T>>;

	private defaultValue: Optional<T> = Optional.empty();

	protected constructor(defaultValue?: T) {
		super();

		this.archive$ = HermesArchiveSubject.of();

		if (defaultValue !== undefined && defaultValue !== null) {
			this.defaultValue = Optional.of(defaultValue);
		}
	}

	on(key: K): HermesObservable<T> {

		this.tryToInitDefault(key);

		return this.archive$
				   .toObservable()
				   .pipe(
					   hermesFilter(() => this.isNotStopped()),
					   hermesMap((map: KeyMap<K, T>) => {
						   return map.get(key);
					   }),
					   hermesFilter((value: Optional<T>) => value.isPresent()),
					   hermesMap((value: Optional<T>) => value.getValueOrNullOrThrowError()),
					   hermesDistinctUntilChanged(),
					   this.hermesTakeUntil()
				   );
	}

	once(key: K): HermesObservable<T> {

		return this.on(key)
				   .pipe(
					   hermesTake(1)
				   );
	}

	get(key: K): Optional<T> {

		this.tryToInitDefault(key);

		return this.archive.get(key);
	}

	next(key: K, value: T): void {
		this.archive.set(key, value);
		this.archive$.next(this.archive);
	}

	private tryToInitDefault(key: K): void {
		this.defaultValue
			.ifPresent((value) => {

				if (!this.archive.has(key)) {
					this.next(key, value);
				}

			});
	}

}
