import { Type } from '@angular/core';

import { AggregateId } from '../../domain/tactical/aggregate/aggregate-id';
import { DomainEvent } from '../../domain/event/domain-event';
import { ReadModelRootId } from '../read/read-model-root-id';
import { DomainEventBus } from '../../domain/event/domain-event.bus';
import { ReactiveService } from '../../../common/reactive.service';
import { hermesFilter } from '../../../common/stream/operator/hermes.filter';
import { HermesObservable } from '../../../common/stream/observable/hermes.observable';

export abstract class EventRepository<R extends ReadModelRootId, I extends AggregateId> extends ReactiveService {

	protected constructor(private readonly domainEventBus: DomainEventBus) {
		super();
	}

	protected onEvent(aggregateId: R, eventType: Type<DomainEvent<I>>): HermesObservable<DomainEvent<any>> {

		return this.domainEventBus
				   .ofEvent(
					   eventType as any
				   )
				   .pipe(
					   hermesFilter((event: DomainEvent<AggregateId>) => event.getAggregateId().toString() === aggregateId.toString())
				   );
	}
}
