import { HermesObservable } from '../hermes.observable';
import { HermesObserver } from '../hermes.observer';

export function hermesInterval(interval: number): HermesObservable<number> {

	let counter = 0;

	return new HermesObservable<number>((observer: HermesObserver<number>) => {

		const id = setInterval(() => {
			observer.next(counter++);
		}, interval);

		return () => {
			clearInterval(id);
			observer.complete();
		}
	});
}
