import { HermesObservable } from '../hermes.observable';
import { HermesSubscriber } from '../subscriber/hermes.subscriber';
import { HermesSingle } from './hermes.single';

export function singleFromObservable<T>(source: HermesObservable<T>): HermesSingle<T> {

	return new HermesSingle<T>((observer) => {

		const subscriber = new HermesSubscriber({
			next: (value: T) => observer.next(value),
			error: (error: any) => observer.error(error),
			complete: () => {
			}
		});

		const subscription = source.subscribe(
			subscriber
		);

		return subscription.getFinalize();
	});
}
