import { AggregateDefinition } from './aggregate-definition';
import { AggregateFactoryArchive, AggregateRepositoryArchive } from './define';
import { AggregateId } from '../../tactical/aggregate/aggregate-id';
import { AggregateRoot } from '../../tactical/aggregate/aggregate-root';
import { Injector } from '@angular/core';

export class AggregateDefinitionInitializer<I extends AggregateId, A extends AggregateRoot<I>> {

	register(
		definedAggregate: Array<AggregateDefinition<I, A>>,
		injector: Injector,
		aggregateFactoryArchive: AggregateFactoryArchive<I, A>,
		aggregateRepositoryArchive: AggregateRepositoryArchive<I, A>,
	): void {

		if (definedAggregate) {

			const set = new Set();
			const filteredDefinitions: Array<AggregateDefinition<I, A>> = [];

			definedAggregate.filter((def: AggregateDefinition<I, A>) => {
				if (!set.has(def.key)) {
					set.add(def.key);
					filteredDefinitions.push(def);
				}
			});

			filteredDefinitions.forEach((def: AggregateDefinition<I, A>) => {

				const factory = injector.get(def.factory),
					repository = injector.get(def.repository);

				if (!aggregateFactoryArchive.has(def.key)) {
					aggregateFactoryArchive.add(def.key, factory);
				}

				if (!aggregateRepositoryArchive.has(def.key)) {
					aggregateRepositoryArchive.add(def.key, repository);
				}
			});

			// console.log('Registered aggregate definition:' + filteredDefinitions.length);
			// console.log('Registered aggregate definition:', filteredDefinitions);
		}
	}

}
