import { AggregateId } from '../core/domain/tactical/aggregate/aggregate-id';

import { AggregateEvent } from '../core/domain/tactical/aggregate/aggregate-event';
import { DomainEvent } from '../core/domain/event/domain-event';
import { HermesObservable } from '../common/stream/observable/hermes.observable';

declare const expect: any;
declare const it: any;
declare const jest: any;

export function assertDomainEvents<I extends AggregateId>(actualEvents: ReadonlyArray<DomainEvent<I>>, expectedEvents: Array<DomainEvent<I>>): void {

	expect(actualEvents.length).toEqual(expectedEvents.length);

	for (const actualEvent of actualEvents) {

		const expectedEvent = expectedEvents.find((event: DomainEvent<I>) => {
			return event.equalsByType(actualEvent);
		});

		expect(expectedEvent).toBeDefined();
		expect(actualEvent.equalsByType(expectedEvent)).toBeTruthy();
		// expect(actualEvent.aggregateId).toEqual(expectedEvent.aggregateId, 'Events aggregateId should be the same');
		expect(actualEvent.getPayload()).toEqual(expectedEvent.getPayload(), 'Events payload should be the same');
	}

}

export function assertAggregateEvents<T extends AggregateId>(actualEvents: ReadonlyArray<AggregateEvent<T>>, expectedEvents: Array<AggregateEvent<T>>): void {

	expect(actualEvents.length).toEqual(expectedEvents.length, 'Aggregate events');

	for (const actualEvent of actualEvents) {

		const expectedEvent = expectedEvents.find((event: AggregateEvent<T>) => {
			return event.equals(actualEvent);
		});

		expect(expectedEvent).toBeDefined();
	}
}

export function testEventRepositoryIsEmptyOnStart<T>(createStream: () => HermesObservable<T>, desc: string): void {

	it('should be no ' + desc + ' events on start', () => {

		expect.assertions(3);

		// given
		const nextFn = jest.fn(),
			errorFn = jest.fn(),
			completeFn = jest.fn();

		// when
		createStream()
			.subscribe(
				() => nextFn(),
				(err: any) => errorFn(err),
				() => completeFn()
			);

		// then
		expect(nextFn).not.toHaveBeenCalled();
		expect(errorFn).not.toHaveBeenCalled();
		expect(completeFn).not.toHaveBeenCalled();
	});
}

export function testWarehouseDefaultValueOnStart<T>(createStream: () => HermesObservable<T>, defaultValue: T, desc: string): void {

	it('should have default value ' + desc, () => {

		expect.assertions(4);

		// given
		const nextFn = jest.fn(),
			errorFn = jest.fn(),
			completeFn = jest.fn();

		// when
		createStream()
			.subscribe(
				(value) => nextFn(value),
				(err: any) => errorFn(err),
				() => completeFn()
			);

		// then
		expect(nextFn).toHaveBeenCalledWith(defaultValue);
		expect(nextFn).toHaveBeenCalledTimes(1);
		expect(errorFn).not.toHaveBeenCalled();
		expect(completeFn).not.toHaveBeenCalled();
	});
}

export function testWarehouseDefaultValueOnStartOnce<T>(createStream: () => HermesObservable<T>, defaultValue: T, desc: string): void {

	it('should have default value ' + desc, () => {

		expect.assertions(4);

		// given
		const nextFn = jest.fn(),
			errorFn = jest.fn(),
			completeFn = jest.fn();

		// when
		createStream()
			.subscribe(
				(value) => nextFn(value),
				(err: any) => errorFn(err),
				() => completeFn()
			);

		// then
		expect(nextFn).toHaveBeenCalledWith(defaultValue);
		expect(nextFn).toHaveBeenCalledTimes(1);
		expect(errorFn).not.toHaveBeenCalled();
		expect(completeFn).toHaveBeenCalledTimes(1);
	});
}
