import { DomainEventHandlerImpl } from '../handler/domain-event-handler-impl';
import { AggregateId } from '../../tactical/aggregate/aggregate-id';
import { DomainEvent } from '../domain-event';
import { DomainEventBus } from '../domain-event.bus';
import { Reactive } from '../../../../common/reactive';


export class DomainEventHandlerInitializer<I extends AggregateId, E extends DomainEvent<I>> extends Reactive {

	init(
		eventHandlers: Array<DomainEventHandlerImpl<I, E>>,
		domainEventBus: DomainEventBus
	): void {

		if (eventHandlers) {

			const set = new Set();
			const filteredHandlers: Array<DomainEventHandlerImpl<I, E>> = [];

			eventHandlers.filter((handler: DomainEventHandlerImpl<I, E>) => {
				if (!set.has((handler as any).domainEventHandler)) {
					set.add((handler as any).domainEventHandler);
					filteredHandlers.push(handler);
				}
			});

			filteredHandlers.forEach((handler: DomainEventHandlerImpl<I, E>) => {
				domainEventBus
					.ofEventHandler(handler)
					.pipe(this.hermesTakeUntil())
					.subscribe((event: E) => {
						handler.handleEvent(event);
					});
			});

			// console.log('Registered event handlers:', filteredHandlers.length)
			// console.log('Registered event handlers:', filteredHandlers)
		}
	}
}
