import { HermesSubscription } from '../subscription/hermes.subscription';
import { HermesSubscriber } from './subscriber/hermes.subscriber';
import { HermesObserver } from './hermes.observer';


export interface HermesOperatorFunction<T, R> {
	(source: HermesObservable<T>): HermesObservable<R>
}

export class HermesObservable<T> {

	private source: HermesObservable<T>;

	// private operator: HermesObservable<T>;

	protected readonly generatorFn: any;

	private generatorFinalize: () => void;

	constructor(generator?: (observer?: HermesObserver<T>) => void) {
		this.generatorFn = generator;
	}

	pipe<T, R>(...operations: Array<HermesOperatorFunction<any, any>>): HermesObservable<any> {
		this.source = this;

		for (const operation of operations) {
			this.source = this.innerPipe(operation, this.source);
		}
		return this.source;
	}

	private innerPipe<T, R>(operation: HermesOperatorFunction<T, R>, stream$: HermesObservable<T>): HermesObservable<R> {
		return ((input: any) => {
			return operation(input);
		})(stream$);
	}

	subscribe(
		next?: (value: T) => void,
		error?: (error: any) => void,
		complete?: () => void
	): HermesSubscription;
	subscribe(
		subscriber: HermesSubscriber<T>
	): HermesSubscription;
	subscribe(
		observer: Partial<HermesObserver<T>>
	): HermesSubscription;
	subscribe(
		arg: any
	): HermesSubscription {

		let subscriber: HermesSubscriber<T>;

		if (arg instanceof HermesSubscriber) {
			subscriber = arg;
		} else if (arg !== null && this.isObserver(arg)) {

			const { next, error, complete } = arg;

			subscriber = this.createSubscriber(next, error, complete);
		} else {

			subscriber = this.createSubscriber(arguments[0], arguments[1], arguments[2]);
		}

		if (this.generatorFn) {
			this.generatorFinalize = this.generatorFn(subscriber);
			subscriber.setFinalize(this.generatorFinalize);
		}

		return this.getSubscription();
	}

	protected createSubscriber(
		next?: (value: T) => void,
		error?: (error: any) => void,
		complete?: () => void
	): HermesSubscriber<T> {
		return new HermesSubscriber({
			next,
			error,
			complete
		});
	}

	protected getSubscription(): HermesSubscription {
		return new HermesSubscription(this.generatorFinalize);
	}

	private isObserver(observer: any): boolean {
		return typeof observer === 'object';
	}

}
