import { Injectable } from '@angular/core';

import { DomainEvent } from './domain-event';
import { DomainEventStream } from './domain-event.stream';
import { AggregateId } from '../tactical/aggregate/aggregate-id';
import { DomainEventHandlerImpl } from './handler/domain-event-handler-impl';
import { HermesObservable } from '../../../common/stream/observable/hermes.observable';
import { hermesFilter } from '../../../common/stream/operator/hermes.filter';
import { HermesSubscription } from '../../../common/stream/subscription/hermes.subscription';
import { HermesSubscriber } from '../../../common/stream/observable/subscriber/hermes.subscriber';

@Injectable()
export class DomainEventBus<E = DomainEvent<AggregateId>> extends HermesObservable<E> {

	constructor(private readonly eventStream: DomainEventStream) {
		super();
	}

	subscribe(
		next?: (value: E) => void,
		error?: (error: any) => void,
		complete?: () => void
	): HermesSubscription;
	subscribe(
		subscriber: HermesSubscriber<E>
	): HermesSubscription;
	subscribe(
		arg: any
	): HermesSubscription {

		return this.eventStream.toObservable().subscribe(arguments[0], arguments[1], arguments[2]);
	}

	ofEvent<E2 extends E>(...events: Array<typeof DomainEvent>): HermesObservable<any> {
		return (this as DomainEventBus<any>)
			.eventStream
			.toObservable()
			.pipe(
				hermesFilter((domainEvent: DomainEvent<AggregateId>) => {
					return events.some((event: typeof DomainEvent) => {
						const eventInstance = this.createEventInstance(event);
						return eventInstance.equalsByType(domainEvent);
					});
				})
			);
	}

	ofEventHandler<E2 extends E>(...handlers: Array<DomainEventHandlerImpl<AggregateId, DomainEvent<AggregateId>>>): any {
		return (this as DomainEventBus<any>)
			.eventStream
			.toObservable()
			.pipe(
				hermesFilter((event: DomainEvent<AggregateId>) => {
					return handlers.some((handler: DomainEventHandlerImpl<AggregateId, DomainEvent<AggregateId>>) => handler.forEvents([event]));
				})
			);
	}

	private createEventInstance(event: typeof DomainEvent): DomainEvent<AggregateId> {
		const args: Array<void> = [],
			argumentLength = event.constructor.length;

		args.fill(undefined, 0, argumentLength);

		return (new (event as any)(...args));
	}

}
