import { HermesObservable, HermesOperatorFunction } from '../observable/hermes.observable';
import { HermesSubscriber } from '../observable/subscriber/hermes.subscriber';
import { hermesEmpty } from '../observable/creator/hermes.empty';


export function hermesTake<T>(valuesNumber: number): HermesOperatorFunction<T, T> {

	return (source) => {

		if (valuesNumber === 0) {
			return hermesEmpty();
		}

		return new HermesObservable(observer => {

			let index = 0;

			const subscriber = new HermesSubscriber({
				next: (v: T) => {
				},
				error: (e: any) => observer.error(e),
				complete: () => observer.complete()
			});

			(subscriber as any).observer.next = function(v: any) {
				if (index < valuesNumber) {
					observer.next(v);
					index++;

					if (index === valuesNumber) {
						subscriber.complete();
					}
				}
			};

			const subscription = source.subscribe(
				subscriber
			);

			return subscription.getFinalize();
		});
	};
}
