import { HermesObservable } from '../hermes.observable';
import { HermesSubscriber } from '../subscriber/hermes.subscriber';
import { HermesSingleSubscriber } from '../subscriber/hermes.single-subscriber';


export class HermesSingle<T> extends HermesObservable<T> {

	protected createSubscriber(
		next?: (value: T) => void,
		error?: (error: any) => void,
		complete?: () => void
	): HermesSubscriber<T> {

		return new HermesSingleSubscriber({
			next,
			error,
			complete
		});
	}
}
