import { Injectable } from '@angular/core';

import { DomainEvent } from './domain-event';
import { AggregateId } from '../tactical/aggregate/aggregate-id';
import { HermesSubject } from '../../../common/stream/observable/hermes.subject';
import { hermesOf } from '../../../common/stream/observable/creator/hermes.of';
import { hermesTake } from '../../../common/stream/operator/hermes.take';
import { hermesFilter } from '../../../common/stream/operator/hermes.filter';
import { HermesObservable } from '../../../common/stream/observable/hermes.observable';


@Injectable()
export class DomainEventStore {

	private readonly domainEvents: Array<DomainEvent<AggregateId>> = [];

	private readonly domainEvents$: HermesSubject<DomainEvent<AggregateId>> = new HermesSubject<DomainEvent<AggregateId>>();

	next(event: DomainEvent<AggregateId>): void {
		this.domainEvents.push(event);
		this.domainEvents$.next(event);
	}

	findEventByType(eventType: string): DomainEvent<AggregateId> {

		const events: Array<DomainEvent<AggregateId>> = this.getEvents();

		return events.reverse()
					 .find((event: DomainEvent<AggregateId>) => {
						 return event.constructor.name === eventType;
					 });
	}

	/**
	 * First tries to event event in the history,
	 * than method waits for future occurrences of the event.
	 */
	waitForEvent(eventType: string): HermesObservable<DomainEvent<AggregateId>> {

		// find in a history
		const event = this.findEventByType(eventType);

		if (event) {
			return hermesOf(event);
		}

		// wait for future occurrence
		return this.waitForNextEventOccurrence(eventType);
	}

	waitForNextEventOccurrence(eventType: string): HermesObservable<DomainEvent<AggregateId>>;
	waitForNextEventOccurrence(event: DomainEvent<AggregateId>): HermesObservable<DomainEvent<AggregateId>>;
	waitForNextEventOccurrence(arg: string | DomainEvent<AggregateId>): HermesObservable<DomainEvent<AggregateId>> {

		let eventType: string;

		if (arg instanceof DomainEvent) {
			eventType = arg.constructor.name;
		} else if (typeof arg === 'string') {
			eventType = arg;
		} else {
			return new HermesObservable((observer) => {
				observer.error(new Error('Unsupported argument type.'));
			});
			// return throwError(new Error('Unsupported argument type.'));
		}

		return this.domainEvents$
				   .toObservable()
				   .pipe(
					   hermesFilter((event) => event.constructor.name === eventType),
					   hermesTake(1)
				   );
	}

	private getEvents(): Array<DomainEvent<AggregateId>> {
		return this.domainEvents;
	}

}
