import { HermesObservable } from '../../common/stream/observable/hermes.observable';
import { Observable } from 'rxjs';

export function fromRxJsObservable<T>(source$: Observable<T>): HermesObservable<T> {
	return new HermesObservable((observer) => {
		const subscription = source$.subscribe(
			(value: T) => observer.next(value),
			(error: any) => observer.error(error),
			() => observer.complete()
		);
		return () => subscription.unsubscribe();
	});
}
