import { HermesObservable, HermesOperatorFunction } from '../observable/hermes.observable';
import { subscriberForOperator } from './operator-utils';


export function hermesToArray<T>(): HermesOperatorFunction<T, Array<T>> {

	return (source: HermesObservable<T>) => {
		return new HermesObservable<Array<T>>((observer) => {

			const values: Array<T> = [];

			const subscriber =
				subscriberForOperator<any>(
					observer,
					(value: T) => {
						values.push(value);
					},
					() => {
						if (values.length > 0) {
							observer.next(values);
						}
						observer.complete();
					}
				);

			return source.subscribe(subscriber).getFinalize();
		});
	};
}
