import { HermesObservable, HermesOperatorFunction } from '../observable/hermes.observable';
import { subscriberForOperator } from './operator-utils';
import { HermesObserver } from '../observable/hermes.observer';


export function hermesMap<T, R>(convert: (value: T, index: number) => R): HermesOperatorFunction<T, R> {

	return (source: HermesObservable<T>) => {
		return new HermesObservable<R>((observer: HermesObserver<any>) => {
			let index = 0;

			const subscriber = subscriberForOperator(observer, (value: T) => {
				observer.next(convert(value, index++));
			});

			return source.subscribe(subscriber).getFinalize();
		});
	};
}
