import { AggregateId } from '../../tactical/aggregate/aggregate-id';
import { Reactive } from '../../../../common/reactive';
import { CommandHandlerImpl } from '../handler/command-handler-impl';
import { CommandBus } from '../command.bus';
import { AggregateRoot } from '../../tactical/aggregate/aggregate-root';
import { Command } from '../command';
import { CreateAggregateCommandHandlerImpl } from '../create-aggregate/create-aggregate.command-handler-impl';


export class CommandHandlerInitializer<I extends AggregateId, A extends AggregateRoot<I>, C extends Command<I>> extends Reactive {

	register(
		commandHandlers: Array<CommandHandlerImpl<I, A, C>>,
		commandBus: CommandBus
	): void {

		if (commandHandlers) {

			const set = new Set();
			const filteredHandlers: Array<CommandHandlerImpl<I, A, C>> = [];

			commandHandlers.filter((handler: CommandHandlerImpl<I, A, C>) => {
				if (!set.has((handler as any).commandHandler)) {
					set.add((handler as any).commandHandler);
					filteredHandlers.push(handler);
				}
			});

			filteredHandlers.forEach((handler: CommandHandlerImpl<I, A, C>) => {
				commandBus
					.ofCommandHandler(handler)
					.pipe(this.hermesTakeUntil())
					.subscribe((command: C) => {
						handler.handleCommand(command);
					});
			});

			// console.log('Registered command handlers:' + filteredHandlers.length);
			// console.log('Registered command handlers:', filteredHandlers);
		}

	}

	registerAggregateCommandHandlers(
		aggregateCommandHandlers: Array<CreateAggregateCommandHandlerImpl<I, A, C>>,
		commandBus: CommandBus
	) {

		if (aggregateCommandHandlers) {

			const set = new Set();
			const filteredHandlers: Array<CreateAggregateCommandHandlerImpl<I, A, C>> = [];

			aggregateCommandHandlers.filter((handler: CreateAggregateCommandHandlerImpl<I, A, C>) => {
				if (!set.has((handler as any).createAggregateCommandHandler)) {
					set.add((handler as any).createAggregateCommandHandler);
					filteredHandlers.push(handler);
				}
			});

			filteredHandlers.forEach((handler: CreateAggregateCommandHandlerImpl<I, A, C>) => {
				commandBus
					.ofCreateAggregateHandler(handler)
					.pipe(this.hermesTakeUntil())
					.subscribe((command: C) => {
						handler.handleCommand(command);
					});
			});

			// console.log('Registered create command handlers:' + filteredHandlers.length);
			// console.log('Registered create command handlers:', filteredHandlers);
		}
	}

}
