import { HermesObservable, HermesOperatorFunction } from '../observable/hermes.observable';
import { HermesObserver } from '../observable/hermes.observer';
import { subscriberForOperator } from './operator-utils';


export function hermesDistinctUntilChanged<T>(compareFn?: (one: T, two: T) => boolean): HermesOperatorFunction<T, T> {

	const compare = compareFn ? compareFn : defaultCompareFn;

	return (source: HermesObservable<T>) => {
		return new HermesObservable<T>((observer: HermesObserver<any>) => {

			let previousValue: T = null;

			const subscriber = subscriberForOperator(observer, (value: T) => {

				if (previousValue === null || !compare(previousValue, value)) {
					previousValue = value
					observer.next(value);
				}
			});

			return source.subscribe(subscriber).getFinalize();
		});
	};
}

function defaultCompareFn<T>(a: T, b: T): boolean {
	return a === b;
}
