import { HermesObservable, HermesOperatorFunction } from '../observable/hermes.observable';
import { HermesSubscriber } from '../observable/subscriber/hermes.subscriber';
import { subscriberForOperator } from './operator-utils';
import { HermesObserver } from '../observable/hermes.observer';


export function hermesSwitchMap<T, R>(operation: (value: T) => HermesObservable<R>): HermesOperatorFunction<T, R> {

	return (source: HermesObservable<T>) => {
		return new HermesObservable<R>((observer: HermesObserver<any>) => {

			let isOuterCompleted = false;
			let innerSubscriber: HermesSubscriber<T> | null = null;

			const tryToComplete = function() {
				if (isOuterCompleted && !innerSubscriber) {
					observer.complete();
				}
			};

			const outerSubscriber = subscriberForOperator<T>(
				observer,
				(v: T) => {

					if (innerSubscriber) {
						innerSubscriber.unsubscribe();
					}

					const subscriber = new HermesSubscriber({
						next: (v2: any) => observer.next(v2),
						error: (e: any) => observer.error(e),
						complete: () => {
							innerSubscriber = null;
							tryToComplete();
						}
					});

					innerSubscriber = subscriber;

					const innerSubscription = operation(v).subscribe(
						subscriber
					);

					return innerSubscription.getFinalize();
				},
				() => {
					isOuterCompleted = true;
					tryToComplete();
				}
			);

			return source.subscribe(outerSubscriber).getFinalize();
		});
	};
}
