import { HermesObserver } from '../observable/hermes.observer';
import { HermesSubscriber } from '../observable/subscriber/hermes.subscriber';

export function subscriberForOperator<T>(
	observer: HermesObserver<T>,
	next?: (value: T) => void,
	complete?: () => void): HermesSubscriber<T> {

	let nextFn = next ? next : (v: T) => {
			observer.next(v);
		},
		completeFn = complete ? complete : () => {
			observer.complete();
		};

	return new HermesSubscriber({
		next: nextFn,
		error: (e: any) => observer.error(e),
		complete: completeFn
	});
}

export function subscriberFromObserver<T>(observer: HermesObserver<T>): HermesSubscriber<T> {
	return new HermesSubscriber({
		next: (v: T) => observer.next(v),
		error: (e: any) => observer.error(e),
		complete: () => observer.complete()
	});
}
