import { HermesSubject } from './hermes.subject';
import { HermesSubscription } from '../subscription/hermes.subscription';
import { HermesSubscriber } from './subscriber/hermes.subscriber';

export class HermesReplaySubject<T> extends HermesSubject<T> {

	private readonly values: Array<T> = [];

	constructor(private readonly bufferSize: number = 1) {
		super();
	}

	next(value: T) {
		this.values.push(value);
		if (this.bufferSize < this.values.length) {
			this.values.shift();
		}
		super.next(value);
	}

	subscribe(
		next?: (value: T) => void,
		error?: (error: any) => void,
		complete?: () => void
	): HermesSubscription;
	subscribe(
		subscriber: HermesSubscriber<T>
	): HermesSubscription;
	subscribe(
		arg: any
	): HermesSubscription {

		let subscriber: HermesSubscriber<T>;

		if (arg instanceof HermesSubscriber) {
			subscriber = arg;
		} else {
			subscriber = this.createSubscriber(arguments[0], arguments[1], arguments[2]);
		}

		const subscription = super.subscribe(subscriber);

		for (const value of this.values) {
			subscriber.next(value);
		}

		return subscription;
	}

}
