import { HermesSubscriber } from './hermes.subscriber';

export class HermesSingleSubscriber<T> extends HermesSubscriber<T> {

	next(value: T): void {

		if (this.isCompleted()) {
			return;
		}

		const observer = this.getObserver();

		if (observer && observer.next) {
			observer.next(value);
			this.complete();
		}
	}

}
